import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    addresses: [],
    newAdress: ''
  },
  mutations: {
    increment(state) {
      state.count++
    },
    mounted(state) {
      if (localStorage.getItem('addresses')) {
        try {
          state.addresses = JSON.parse(localStorage.getItem('addresses'));
        } catch (e) {
          localStorage.removeItem('addresses');
        }
      }
    },
    
    addAdress: function (state) {

      if (!state.newAdress) {
        return;
      }

      state.addresses.push(state.newAdress);
      state.newAdress = '';
      const parsed = JSON.stringify(state.addresses);
      localStorage.setItem('addresses', parsed);
    },
    removeAdres(state, x) {
      this.addresses.splice(x, 1);
      const parsed = JSON.stringify(state.addresses);
      localStorage.setItem('addresses', parsed);
    }
  }
})